.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: -20%rem;
    background-color: var(--title-color);
    opacity: .8;
    padding: .2rem .5rem;
    border-radius: .4rem;
    z-index: var(--z-tooltip);
    transition: .4s;
}

.scrollup__icon {
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover {
    background-color: var(--title-color-dark);
}

.show-scroll {
    bottom: 3rem;
}

/* Breakpoints */
@media screen and (max-width: 992px) { 
    .scrollup {
        right:  1.5rem;
        padding: .25rem .4rem;
    }
    
    .scrollup__icon {
        font-size: 1.25rem;
    }
}

@media screen and (max-width: 350px) { 
    .scrollup {
        right: 1rem;
    }
}
