.header {
    width            : 100%;
    position         : fixed;
    top              : 0;
    left             : 0;
    z-index          : var(--z-fixed);
    background-color : var(--body-color);
    box-shadow       : 0 -1px 4px rgba(0, 0, 0, 0.15);
}

.nav {
    height          : calc(var(--header-height));
    display         : flex;
    justify-content : space-between;
    align-items     : center;
    column-gap      : 1rem;
}


.nav__logo,
.nav__toggle {
    color           : var(--title-color);
    font-weight     : var(--font-medium);
}

.nav__list {
    display         : flex;
    column-gap      : 2rem;
}

.nav__link {
    display         : flex;
    flex-direction  : column;
    align-items     : center;
    font-size       : var(--small-font-size);
    color           : var(--title-color);
    font-weight     : var(--font-medium);
    transition      : .3s;
}

.nav__icon,
.nav__close,
.nav__toggle {
    display         : none;
}

.active-link,
.nav__link:hover {
    color           : darkorange;
}

/* BREAKPOINTS */

/* Medium Device */

@media screen and (max-width: 768px) {
    .header {
        top                 : initial;
               
    }

    .nav {
        height              : var(--header-height);
    }

    .nav__menu {
        position            : fixed;     
        left                : 0;
        top                 : -100%;
        width               : 100%;
        background-color    : var(--body-color);
        padding             : 1rem 2rem 1.5rem 4rem;
        box-shadow          : 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius       : 0 0 1.5rem 1.5rem;
        transition          : .3s;
    }

    .show-menu {
        top: 0;
    }

    .nav__list {
        display                 : grid;
        grid-template-columns   : repeat(4, 1fr);
        gap                     : 2rem;
    }

    .nav__icon {
        font-size           : 1.2rem;
    }

    .nav__close {
        position            : absolute;
        right               : .5rem;
        top                 : .2rem;
        font-size           : 1.2rem;
        cursor              : pointer;
        color               : var(--title-color);
    }

    .nav__close:hover {
        color               : var(--title-color-dark);
    }
    
    .nav__toggle {
        font-size           : 1.1rem;
        cursor              : pointer;
    }

    .nav__icon,
    .nav__close,
    .nav__toggle {
        display             : block;
    }
}

/* Small Device */ 

@media screen and (max-width: 350px) { 
    .nav__menu {
        padding             : 1rem 1.5rem 1.3rem 1rem;
    }
    
    .nav__list {
        column-gap          : 0;
    }

    .nav__close {
        position            : absolute;
        right               : 0.5rem;
        top                 : 0.2rem;
        font-size           : 1.2rem;
        cursor              : pointer;
        color               : var(--title-color);
    }

    .nav__close:hover {
        color               : var(--title-color-dark);
    }
}